import { getQuery } from '@/util/cookie';
import { getUserCenter, userCenterInfo } from '@/api/user';
import { login } from '@/api/login';
import { hasLogin } from '@/util/session';

@Component({
  components: {}
})
export default class User extends Vue {
  private sid: string = getQuery('sellerid') || '';
  private userCenter: userCenterInfo = {
    name: '',
    free_task_time: '',
    refund_task_time: '',
    bonus_point: '',
    taobao_account: '',
    jd_account: '',
    phone: '',
    refund_list: []
  };
  private created() {
    if (!hasLogin()) {
      console.log('sid======' + this.sid);
      login(this.sid, '');
    } else {
      getUserCenter()
        .then((res: any) => {
          this.userCenter = res;
        })
        .catch((res: { message: string }) => {
          this.$toast(res.message);
          // this.userCenter = {
          //   name: '哈呦',
          //   free_task_time: '12',
          //   refund_task_time: '13',
          //   bonus_point: '14',
          //   taobao_account: '333ed',
          //   jd_account: 'eqwe',
          //   phone: '15899992222',
          //   refund_list: [
          //     {
          //       execute_time: '2018-07-27',
          //       id: '2',
          //       refund: '3',
          //       status: '4'
          //     },
          //     {
          //       execute_time: '2018-07-28',
          //       id: '22',
          //       refund: '32',
          //       status: '3'
          //     }
          //   ]
          // };
        });
    }
  }
}



